.animal-show {
    position: relative;
    border: 1px solid lightgoldenrodyellow;
    pad: 10px;
    border-radius: 5px;
    margin: 10px;
    box-shadow: 0px 3px 4px lightgray;
}

.animal {
    height: 200px;
}

.heart {
    position: absolute;
    bottom: 10%;
    right: 10%;
    opacity: 60%;
}